.footer-top { 
  text-align: left;
  border: none; 
}

.footer-top img {
  border: 0;
  width: 120px;
  margin-bottom: 30px;
}

.footer-contact i { 
  padding-right: 10px; 
  font-size: 18px; 
  color: #fff; 
}

.footer-contact p a:hover, .footer-contact p a:focus { 
  border-bottom: 1px #0066ff;
  text-decoration: none;
}

.footer-links a { 
  color: #fff; 
  border: 0; 
}
.footer-links a:hover, .footer-links a:focus { 
  color: #0066ff; 
  text-decoration: none; 
}

.footer-bottom { 
  float: right;
}

.footer-social a { 
  margin: 0 10px; 
  color: #fff; 
  border: 0; 
}
.footer-social a:hover, .footer-social a:focus { 
  color: #0066ff; 
  border: 0; 
}
.footer-social i { 
  font-size: 24px; 
  vertical-align: middle; 
}

.scrollTop {
  position: fixed; 
  width: 100%;
  bottom: 20px;
  align-items: center;
  height: 20px;
  justify-content: center;
  z-index: 1000;
  cursor: pointer;
  -webkit-animation: fadeIn 0.3s;
          animation: fadeIn 0.3s;
  transition: opacity 0.4s;
  opacity: 0.3;
}

.scrollTop:hover{
  opacity: 1;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.3;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.3;
  }
}


